.modal {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 100000;
	&_fon {
		background: #F6FCFF;
		opacity: .8;
		position: fixed;
		width: 100%;
		height: 100%; }
	&_modaltext {
		color: #000;
		font-size: 30px;
		padding: 20px 40px;
		border: 2px solid #AEAEAE;
		background: #F6FCFF;
		position: fixed;
		top: 80px;
		left: 50%;
		margin-left: -30%;
		width: 60%;
		box-shadow: 0 4px 10px 2px rgba(0,0,0,0.5); }
	&_closeblock {
		cursor: pointer;
		position: fixed;
		line-height: 60px;
		font-size: 82px;
		transform: rotate(45deg);
		text-align: center;
		top: 20px;
		right: 30px;
		color: #337AB7;
		&:hover {
			color: #000; } } }
