* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: DM Mono;
	font-size: 16px; }

@import "modal";

html {
	overflow-x: hidden; }

p {
	margin-bottom: 0; }

.clear {  // перенос блока
	clear: both;
	height: 0; }

.header {
	min-height: 700px;
	color: #858281;
	padding-top: 15px;
	background: url("../src/img/logo.jpg") center center/cover no-repeat; }

.navi {
	display: flex;
	justify-content: space-between;
	&_left {
		display: flex; }
	&_span {
		span {
			background: #BEA67C;
			display: block;
			width: 34px;
			height: 2px;
			margin-top: 9px; } }
	&_menu {
		display: block;
		font-size: 20px;
		margin-left: 8px;
		margin-top: 6px; }
	&_center {
		display: flex;
		p {
			margin-right: 40px;
			margin-top: 11px; } }
	&_wooder {
		margin-right: 70px;
		margin-top: 9px;
		&:hover {
			animation: wooder 0.6s ease;
			animation-iteration-count: 1; } }
	&_right {
		display: flex;
		img {
			width: 23px;
			height: 23px;
			margin-top: 13px;
			margin-right: 20px;
			&:hover {
				transition: 0.5s all;
				transform: scale(2);
				filter: sepia(100%); } } // замена цвета эффектом
		p {
			margin-top: 13px; } } }

@keyframes wooder {
	15% {
		transform: translateX(9px); }
	30% {
		transform: translateX(-9px); }
	40% {
		transform: translateX(6px); }
	50% {
		transform: translateX(-6px); }
	65% {
		transform: translateX(3px); }
	100% {
		transform: translateX(0); } }

.info {
	margin-top: 170px;
	display: flex;
	justify-content: center;
	&_number {
		width: 50px;
		span {
			display: block;
			width: 2px;
			height: 50px;
			background: #ffffff;
			margin-left: 20px;
			color: #BEA67C; }
		p {
			font-size: 40px;
			font-weight: 900;
			transform: rotate(90deg);
			color: #BEA67C;
			margin-top: 10px; } }
	&_dot {
		span {
			width: 5px;
			height: 5px;
			border-radius: 100%;
			margin-top: 30px;
			opacity: 0.3;
			&:first-child {
				opacity: 0.9; } } }
	&_main {
		margin-left: 130px;
		margin-right: 260px;
		h1 {
			font-size: 100px;
			color: #BEA67C;
			font-weight: 900;
			&:hover {
				transition: 1.5s all;
				color: #bea70b; } } }
	&_arrow {
		margin-top: 20px;
		width: 200px;
		height: 35px;
		display: flex;
		justify-content: center;
		border: 2px solid #fff;
		p {
			font-size: 12px;
			font-weight: 600;
			margin-top: 6px; }
		img {
			width: 20px;
			height: 15px;
			margin-left: 10px;
			margin-top: 8px; } }
	&_block {
		width: 15px;
		display: flex;
		flex-direction: column;
		img {
			width: 15px;
			height: 50px;
			margin-top: 15px; }
		p {
			transform: rotate(90deg);
			font-size: 12px;
			margin-top: 15px; } }
	&_mouse {
		a {
			text-decoration: none;
			&:focus { // убирает рамку-точки у посещённых ссылок
				outline: none; } }
		img {
			width: 15px;
			height: 20px;
			margin-top: 50px; } }
	&_share {
		text-align: right; } }

.disain {
	position: relative;
	min-height: 700px;
	margin-top: 10px;
	&_block-up {
		width: 50%; }
	&_block-down {
		margin-top: 90px;
		margin-left: auto;
		// margin-right: 0
		width: 50%;
		text-align: right; }
	&_long-line {
		display: flex;
		span {
			display: block;
			width: 108px;
			height: 1px;
			background: #000;
			margin-top: 23px; }
		p {
			text-transform: uppercase;
			font-size: 30px;
			font-weight: 900;
			margin-left: 20px;
			margin-right: 20px; } }
	&_long-line_right {
		justify-content: flex-end; }
	&_little-line {
		display: flex;
		margin-left: 70px;
		margin-right: 90px;
		span {
			margin-top: 45px;
			margin-left: 5px;
			display: block;
			width: 90px;
			height: 2px;
			background:  #BEA67C; }
		p {
			margin-left: 5px; } }
	&_little-line-down {
		span {
			width: 30px; } }
	&_right-arrow {
		display: flex;
		justify-content: center;
		margin-top: 50px;
		img {
			width: 35px;
			height: 11px;
			margin-left: 10px;
			margin-top: 2px; }
		p {
			font-size: 10px;
			font-weight: 900; } }
	&_number {
		margin-top: 50px;
		font-weight: 900;
		font-size: 20px; }
	&_line {
		margin-top: 20px;
		width: 2px;
		height: 30px;
		background: #BEA67C; }
	&_line_right {
		margin-left: auto; }
	&_img_up {
		width: 500px;
		position: absolute;
		right: 0;
		top: -10px; }
	&_img_down {
		width: 500px;
		position: absolute;
		left: 0;
		top: 385px; }
	&_item {
		display: flex;
		justify-content: space-between; }
	&_img-text-up {
		position: absolute;
		z-index: 2;
		right: -220px;
		top: 90px; }
	&_img-text-down {
		position: absolute;
		z-index: 2;
		left: -345px;
		bottom: 140px; } }

.quality {
	padding-top: 50px;
	min-height: 600px;
	span {
		margin: 0 auto;
		display: block;
		width: 40px;
		height: 2px;
		background:  #BEA67C;
		margin-top: 10px; }
	&_title {
		text-align: center;
		font-size: 50px;
		font-weight: 900;
		text-transform: uppercase; }
	&_subtitle {
		text-align: center;
		margin-top: 20px;
		font-size: 12px; }
	&_round {
		display: flex;
		justify-content: space-evenly;
		margin-top: 50px;
		span {
			position: relative;
			z-index: 2;
			margin-top: 20px;
			display: block;
			width: 25px;
			height: 2px; }
		img {
			cursor: pointer;
			&:hover {
				transition: 0.8s all;
				transform: rotateZ(360deg); } } }
	&_descr {
		position: relative;
		z-index: 2;
		text-align: center;
		font-size: 14px;
		margin-top: 10px; } }
.container {
	position: relative; }

.complexity {
	padding-top: 170px;
	min-height: 700px;
	background: url("../src/img/logo3.jpg") center center/cover no-repeat;
	&_title {
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		width: 530px;
		margin-left: 140px;
		p {
			color:#BEA67C {}
			text-transform: uppercase;
			font-weight: 900;
			font-size: 40px; }
		span {
			display: block;
			width: 100px;
			height: 1px;
			background:#fff {}
			margin-top: 30px; } }
	&_subtitle {
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		width: 520px;
		margin-top: 20px;
		margin-left: 210px;
		p {
			color: #fff;
			margin-left: 15px; }
		span {
			display: block;
			width: 130px;
			height: 2px;
			background: #BEA67C;
			margin-top: 13px; } }
	&_block {
		margin: 0 auto;
		display: flex;
		justify-content: center;
		vertical-align: center;
		width: 210px;
		height: 40px;
		border: 2px solid #ffffff;
		margin-left: 260px;
		margin-top: 40px;
		p {
			color: #fff;
			margin-top: 10px;
			margin-left: 10px;
			font-size: 12px; }
		img {
			width: 20px;
			height: 20px;
			margin-top: 7px; } }
	&_line {
		span {
			position: absolute;
			display: block;
			width: 1px;
			height: 70px;
			background:  #ffffff;
			right: 30px;
			bottom: 100px; } } }

.about {
	min-height: 500px;
	padding: 70px 0;
	&_img {
		position: absolute;
		z-index: -1;
		left: -50px;
		top: -100px; }
	&_title {
		display: flex;
		margin-top: 70px;
		margin-left: 460px;
		p {
			font-size: 40px;
			font-weight: 900;
			margin-left: 20px; }
		span {
			display: block;
			width: 130px;
			height: 1px;
			background: #000;
			margin-top: 30px; } }
	&_subtitle {
		display: flex;
		margin-top: 10px;
		margin-left: 560px;
		p {
			width: 360px;
			margin-left: 20px; }
		span {
			display: block;
			width: 30px;
			height: 2px;
			background: #BEA67C;
			margin-top: 10px; } }
	&_arrow {
		display: flex;
		margin: 0 auto;
		margin-top: 20px;
		margin-left: 610px;
		p {
			font-weight: 900;
			font-size: 10px;
			cursor: pointer; } }
	&_arrow-img {
		img {
			width: 40px;
			height: 15px;
			margin-left: 10px;
			cursor: pointer; } } }

footer {
	padding-top: 20px;
	height: 70px;
	background: url("../src/img/logo5.jpg") center center/cover no-repeat;
	text-align: center;
	img {
		&:hover {
			animation: footer-img 0.9s ease;
			animation-iteration-count: 1; } } }

@keyframes footer-img {
	15% {
		transform: translateY(15px); }
	30% {
		transform: translateY(-15px); }
	40% {
		transform: translateY(10px); }
	50% {
		transform: translateY(-10px); }
	65% {
		transform: translateY(5px); }
	100% {
		transform: translateY(0); } }
